                    <input @input="inputChange(20,'courseName')" @click="scollView('courseName')" ref="courseName" class="title" type="text" v-model="q.courseName" placeholder="请输入课程名称">
                </div>
                <div class="from-list">
                    <p>开课时间</p>
                    <div class="from-list-input" @click="show=true">
                        <input class="title title2" v-model="q.startTime" disabled type="text" placeholder="请选择开课时间">
                        <van-icon name="arrow"/>
                    </div>
                </div>
            </div>

            <div class="from">
                <div class="from-list">
                    <p>课程类型</p>
                    <div class="from-list-input">
                        <van-radio-group class="title title2" v-model="q.courseType" direction="horizontal">
                            <van-radio v-for="(i, index) in courseTypeList" :key="index" checked-color="#00B095" :name="i.courseType">{{ i.courseTypeText }}</van-radio>
                        </van-radio-group>
                    </div>
                </div>
            </div>


            <div class="from">
                <div class="from-list">
                    <p>课程简介</p>
                    <div class="from-list-input">
                        <input  @input="inputChange(500,'courseDetail')" @click="scollView('courseDetail')" ref="courseDetail" class="title title2" v-model="q.courseDetail" type="text" placeholder="请输入课程简介">
                    </div>
                </div>
            </div>

            <div class="from">
                <div class="from-list">
                    <p>主讲人</p>
                    <div class="from-list-input">
                        <input @input="inputChange(20,'teacherName')" @click="scollView('teacherName')" ref="teacherName"  class="title title2" v-model="q.teacherName" type="text" placeholder="请输入主讲人">
                    </div>
                </div>
            </div>

            <div class="from">
                <div class="from-list">
                    <p>主讲人头衔</p>
                    <div class="from-list-input">
                        <input @input="inputChange(32,'teacherTitle')" @click="scollView('teacherTitle')" ref="teacherTitle" class="title title2" v-model="q.teacherTitle" type="text" placeholder="请输入主讲人头衔">
                    </div>
                </div>
            </div>

            <div class="from">
                <div class="from-list">
                    <p>讲师介绍</p>
                    <div class="from-list-input">
                        <input @input="inputChange(500,'teacherDetail')" @click="scollView('teacherDetail')" ref="teacherDetail" class="title title2" v-model="q.teacherDetail" type="text" placeholder="请输入讲师介绍">
                    </div>
                </div>
            </div>

            <div class="from">
