                    <img @click="pLoadPDF()" src="../img/icon_upload@2x.png" alt="">
                    <p class="from-list-p">支持文件：pdf,ppt,word,excel,txt</p>
                    <div style="border-radius: 0.08rem;overflow: hidden;margin-top: 0.2rem">
                        <div class="back" v-if="q.fileUrl.length>0">
                            <div class="back-flex" v-for="(item, i) in q.fileUrl" :key="i">
                                <div>
                                    <!--<img src="../img/live_icon_time@2x.png" alt="">-->
                                    {{ item.name }}
                                </div>
                                <van-icon @click="shan(i)" name="cross"/>
                            </div>
                        </div>

                        <div class="back" v-if="upDown" style="display: flex;justify-content: flex-start;align-items: center;font-size: 0.28rem;
    font-weight: 400;
    color: #333333;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;padding-bottom: 0.4rem;
margin-top: 0">
                            <div style="margin-right: 0.2rem">
                                文件上传中，请耐心等待
                            </div>
                            <img style="width: 0.28rem;height: 0.28rem;margin-top: 0" src="./img/jiazai.gif" alt="">
                        </div>
                    </div>
                </div>
            </div>

            <div class="from">
                <div class="from-list">
                    <p>课程类型</p>
                    <div class="from-list-input">
                        <van-radio-group class="title title2" v-model="q.isPlayback" direction="horizontal">
                            <van-radio checked-color="#00B095" name="1">自动录制</van-radio>
                            <van-radio checked-color="#00B095" name="2">无需回放</van-radio>
                        </van-radio-group>
                    </div>
                </div>
            </div>
            <div class="footer">
                <button class="btn" @click="getData">立即申请</button>
            </div>

            <!--时间弹框-->
            <van-action-sheet v-model="show">
                <van-datetime-picker
                        v-model="currentDate"
                        type="datetime"
                        title="选择年月日"
                        :min-date="minDate"
                        :max-date="maxDate"
                        @confirm="dateConfirm"
                        @cancel="show=false"
                        :formatter="formatter"
                />
            </van-action-sheet>
        </div>
    </div>
</template>

<script>
    export default {
        name: "push",
        data() {
            return {
                upDown: false,
                show: false,
                minDate: new Date(),
                maxDate: new Date(2025, 10, 1),
                currentDate: new Date(),
                q: {
                    courseType: '1',
                    isPlayback: '2',
                    courseName: '',
                    coursePictureUrl: '',
                    teacherName: '',
                    teacherTitle: '',
                    startTime: '',
                    teacherDetail: '',
                    courseDetail: '',
                    fileUrl: [],
                },
                courseId: null,
                click: true,
                courseTypeList: []
            }
        },
        watch: {
            inputText(value) {
                this.limitMaxLength();
            }
        },

        created() {
            this.couListFn()
            if (this.$route.query.courseId) {
                this.courseId = this.$route.query.courseId;
                this.detail()
            }else {
                this.$api.info().then((res) => {
                    this.q.teacherName = res.memberName;
                })
            }
        },
        mounted(){

        },
        methods: {
            inputChange(num, id){
                console.log(12321321312)
                this.q[id] = this.q[id].slice(0,num)
            },
            scollView(id){
                let u = navigator.userAgent;
                let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                if (isiOS) {
                } else {
                    setTimeout(()=>{
                        let pannel = this.$refs[id];
                        pannel.scrollIntoView(true);
                        pannel.scrollIntoViewIfNeeded();
                    },200)
                }

            },
            shan(i){
                this.q.fileUrl.splice(i, 1);
            },
            detail() {
                this.$api.detail({
                    "courseId": this.courseId
                }).then(res => {
                    this.q = res;
                    if(this.q.fileUrl == ''){
                        this.q.fileUrl = []
                    }else {
                        this.q.fileUrl = JSON.parse(this.q.fileUrl)
                    }
                })
            },

            couListFn() {
                this.$bs.getVersion({}).then((e) => {
                    this.$api.coursetype({
                        version: e.data.data
                    }).then(res => {
                        this.courseTypeList = res.body;
                    })
                })

            },
            formatter(type, val) {
                if (type === 'year') {
                    return val + '年';
                }
                if (type === 'month') {
                    return val + '月';
                }
                if (type === 'day') {
                    return val + '日';
                }
                if (type === 'hour') {
                    return val + '时';
                }
                if (type === 'minute') {
                    return val + '分';
                }
                return val;
            },

            upImage(){
                this.$bs.upImage({type: '1'}).then((e) => {
                    console.log(e);
                    if (e.status == '1') {
                        this.$toast('上传成功')
                        this.q.coursePictureUrl = e.data.data
                    } else if (e.msg) {
                        this.$toast(e.msg)
                    }

                })
            },
            pLoadPDF(){
                this.upDown = true;
                this.$bs.UDUpLoadPDF({}).then((res)=>{
                    this.upDown = false;
                    console.log(res)
                    this.q.fileUrl.push({
                        url: res.data.data,
                        name: res.data.name
                    })
                    console.log(this.q.fileUrl)
                })

            },
            getData() {
                if(!this.q.courseName){
                    this.$toast('请输入课程名称')
                    console.log(1)
                    return false
                }
                if(!this.q.coursePictureUrl){
                    this.$toast('请选择图片')
                    console.log(2)
                    return false
                }
                if(!this.q.teacherName){
                    this.$toast('请输入主讲人')
                    console.log(3)
                    return false
                }
                if(!this.q.teacherTitle){
                    this.$toast('请输入主讲人头衔')
                    console.log(4)
                    return false
                }
                if(!this.q.startTime){
                    this.$toast('请选择开始时间')
                    console.log(5)
                    return false
                }
                if(!this.q.teacherDetail){
                    this.$toast('请输入讲师介绍')
                    console.log(6)
                    return false
                }
                if(!this.q.courseDetail){
                    this.$toast('请输入课程简介')
                    console.log(7)
                    return false
                }

                if(this.q.fileUrl.length === 0){
                    this.$toast('请上传文件')
                    console.log(7)
                    return false
                }
                if(!this.click){
                    return false;
                }
                this.click = false;
                setTimeout(()=>{
                    this.click = true;
                },1000)
                if (this.courseId) {
                    this.$api.update({
                        ...this.q,
                        fileUrl:JSON.stringify(this.q.fileUrl)
                    }).then(res => {
                        this.click = true;
                        console.log(121213)
                        this.$router.replace('/yaodianbao/openClass/detail?courseId=' + res.courseId)

                    })
                } else {
                    this.$api.create({
                        ...this.q,
                        fileUrl:JSON.stringify(this.q.fileUrl)
                    }).then(res => {
                        this.click = true;
                        console.log(121213)
                        this.$router.replace('/yaodianbao/openClass/detail?courseId=' + res.courseId)
                    })
                }
            },
            afterRead(file) {
                // 此时可以自行将文件上传至服务器
                console.log(file);
                var formData = new FormData(); //构造一个 FormData，把后台需要发送的参数添加
                formData.append('file', file.file); //接口需要传的参数
                this.$api.upload(formData).then(res => {
                    this.q.coursePictureUrl = res.url;
                })
            },
            dateConfirm(e) {
                this.q.startTime = this.dateFormat("YYYY-mm-dd HH:MM", e);
                this.show = false;
            },
            dateFormat(fmt, date) {
                let ret;
                const opt = {
                    "Y+": date.getFullYear().toString(),        // 年
                    "m+": (date.getMonth() + 1).toString(),     // 月
                    "d+": date.getDate().toString(),            // 日
                    "H+": date.getHours().toString(),           // 时
                    "M+": date.getMinutes().toString(),         // 分
                    "S+": date.getSeconds().toString()          // 秒
                    // 有其他格式化字符需求可以继续添加，必须转化成字符串
                };
                for (let k in opt) {
                    ret = new RegExp("(" + k + ")").exec(fmt);
                    if (ret) {
                        fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
                    }
                }
                return fmt;
            }
        },
    }
</script>

